.footer {
	padding: 30px 0;
	background-color: $color-jungle-mist;

	&__link {
		font-size: 14px;
		line-height: 33px;
		text-decoration: none;
		color: $color-hoki;

		span {
			border-bottom: 1px solid rgba($color-black, 0.2);
		}
	}

	&__pay-links {
		display: inline-flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 25px;
		padding: 0 2px;
		background-color: $color-white;
	}

	&__pay-link {
		padding: 5px 9px;
		background-color: $color-white;
	}

	&__trip-advisor {
		margin-bottom: 88px;
	}

	&__search {
		position: relative;
		margin-bottom: 20px;
	}

	&__search-input {
		display: block;
		width: 100%;
		background: #B9D1D8;
		border: 1px solid #7EA1AB;
		box-sizing: border-box;
		border-radius: 4px;
		padding: 9px 40px 9px 13px;
	}

	&__search-icon {
		position: absolute;
		right: 15px;
		top: 50%;
		z-index: 2;
		width: 18px;
		height: 18px;
		transform: translateY(-50%);
		pointer-events: none;
	}

	&__links {
		display: flex;
		flex-flow: row wrap;
		align-items: flex-start;
		justify-content: space-between;
		font-size: 0;
		margin-bottom: 15px;

		.footer__link {
			flex: 0 0 50%;
		}
	}

	&__copy {
		font-size: 16px;
		line-height: 22px;
		color: #164356;
	}
}
