*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}

body {
	font-family: $font-open-sans;
}

.container,
.container-fluid {
	padding-right: 4px;
	padding-left: 4px;
	height: auto;

}

* {
	&:hover,
	&:focus,
	&:active {
		outline: none;
	}
}

.form-row {
	margin-bottom: 24px;
}
