.header {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 100;
	display: flex;
	height: 80px;
	padding: 17px 0;
	background-color: $color-white;
	transition: padding 0.2s, background-color 0.4s;
	flex-flow: row nowrap;
	align-items: center;
	transition-delay: 0s, 0.6s;

	&.fixed {
		padding: 7px 0;
	}

	&.show-menu {
		background-color: transparent;
		transition: padding 0.2s;

		.header {
			&__phone,
			&__sublink {
				opacity: 0;
			}
		}

		.logo {
			opacity: 0;
		}
	}

	&__phone {
		display: block;
		width: auto;
		font-family: $font-open-sans;
		font-weight: 700;
		font-size: 16px;
		line-height: 19px;
		text-decoration: none;
		color: $color-brown-derby;
		transition: opacity 0.4s;
	}

	&__sublink {
		display: inline-flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-start;
		border: none;
		font-size: 12px;
		line-height: 16px;
		padding: 0;
		background: transparent;
		transition: opacity 0.4s;
	}

	&__sublink-text {
		border-bottom: 1px dashed;
		color: $color-curious-blue;
	}

	&__sublink-svg {
		margin-right: 3px;
		flex: 0 0 14px;
		width: 14px;
		height: 14px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__burger {
		position: relative;
		top: 6px;
		display: block;
		border: none;
		padding: 0;
		width: 25px;
		height: 18px;
		background: transparent;
		transform: translateY(-50%);

		&::before,
		&::after {
			content: "";
			position: absolute;
			left: 0;
			display: block;
			width: 25px;
			height: 2px;
			background-color: $color-vermilion;
			transform-origin: center center;
			transition: transform 0.4s, top 0.4s, left 0.4s, background-color 0.4s;
		}

		&::before {
			top: 0;
		}

		&::after {
			top: 100%;
			transform: translateY(-100%);
		}

		span {
			position: absolute;
			left: 0;
			right: 0;
			top: 50%;
			display: block;
			height: 2px;
			background-color: $color-vermilion;
			opacity: 1;
			transform: translateY(-50%);
			transition: opacity 0.4s, background-color 0.4s;
		}

		&.active {
			&::before,
			&::after {
				left: 50%;
				top: 50%;
				background-color: $color-white;
				transition-delay: 0s;
			}

			&::before {
				transform: translate(-50%, -50%) rotate(-45deg);
			}

			&::after {
				transform: translate(-50%, -50%) rotate(45deg);
			}

			span {
				opacity: 0;
				background-color: $color-white;
			}
		}
	}
}
