.review {
	&__title {
		margin-bottom: 25px;
	}
	&__date {
		font-size: 14px;
		line-height: 19px;
		color: #FF8336;
	}

	&__name {
		font-weight: 600;
		font-size: 16px;
		line-height: 22px;
		color: #561F1B;
		margin-bottom: 2px;
	}

	&__city {
		font-size: 14px;
		line-height: 19px;
		color: #561F1B;
	}

	&__content {
		position: relative;
		padding: 20px 15px;
		background-color: #DDEEF3;

		&::before {
			content: "";
			position: absolute;
			right: 60px;
			top: -11px;
			border: solid;
			border-width: 6px 10px;
			border-color: transparent transparent #ddeef3 #ddeef3;
			display: block;
			width: 0;
			height: 0;
		}
	}

	&__text {
		font-weight: 500;
		font-size: 14px;
		line-height: 19px;
		color: #561F1B;
		margin-bottom: 10px;
	}

	&__link {
		font-weight: 600;
		font-size: 12px;
		line-height: 16px;
		text-decoration: none;
		color: #1391E1;
		border-bottom: 1px dashed;
	}

	.container {
		width: auto;
	}
}
