.input-checkbox {
	&__content {
		position: relative;
		font-size: 14px;
		line-height: 19px;
		padding-left: 24px;
	}

	&__icon {
		position: absolute;
		left: 0;
		top: 50%;
		width: 14px;
		height: 14px;
		transform: translateY(-50%);
		border: 1px solid #B1B1B1;
		border-radius: 2px;

		&::before {
			content: "";
			position: absolute;
			top: 4px;
			left: 10px;
			opacity: 0;
			width: 16px;
			height: 13px;
			transform: translate(-50%, -50%);
			transition: opacity 0.2s;
			background: url('../images/sprites/svg/icon-check.svg') no-repeat center center;
		}
	}

	&__input {
		display: none;

		&:checked {
			~ .input-checkbox {
				&__content {
					.input-checkbox__icon {
						&::before {
							opacity: 1;
						}
					}
				}
			}
		}
	}
}
