.create-input {
	&__input {
		position: relative;
		font-size: 14px;
		line-height: 19px;
		background: #F2F2F2;
		border-radius: 4px;
		padding: 10px 14px;
		margin-bottom: 24px;

		input {
			padding: 0;
			font-size: 14px;
			line-height: 19px;
			border: none;
			background-color: transparent;
			pointer-events: none;
		}

		&_template {
			display: none;
		}
	}

	&__close {
		position: absolute;
		right: 8px;
		top: 50%;
		width: 16px;
		height: 16px;
		transform: translateY(-50%);

		svg {
			position: absolute;
			left: 50%;
			top: 50%;
			width: 9px;
			height: 9px;
			transform: translate(-50%, -50%);
		}
	}

	&__dropdown {
		display: block;
		width: 100%;
	}

	&__button {
		display: block;
		padding: 10px 14px;
		width: 100%;
		border-radius: 4px;
		font-size: 14px;
		line-height: 19px;
		background-color: #dfdfdf;

		&::after {
			display: none;
		}
	}

	&__dropdown-menu {
		top: 0;
		left: 0;
		right: 0;
		background: #D2D2D2;
		border-radius: 0 0 4px 4px;
		margin: -4px 0 0;
		padding: 4px 0 0;
		z-index: -1;
		border: none;
		width: 100%;
		max-width: none;
		max-height: 60vh;
		overflow-y: hidden;
	}

	&__dropdown-item {
		font-size: 14px;
		line-height: 19px;
		padding: 7px 14px;
		text-decoration: none;
	}
}
