.advantages-slide {
	padding-top: 26px;
	padding-bottom: 78px;
	text-align: center;

	.container {
		width: auto;
	}

	&__image {
		margin-bottom: 5px;
	}

	&__title {
		font-weight: 700;
		font-size: 24px;
		line-height: 33px;
		color: $color-crusta;
		margin-bottom: 5px;
	}

	&__text {
		font-size: 14px;
		line-height: 19px;
		color: $color-black;
	}
}
