.count-input {
	display: flex;
	width: auto;
	flex-flow: row nowrap;
	align-items: stretch;
	margin-bottom: 24px;

	&_no-margin {
		margin-bottom: 0;
	}

	&_block {
		width: 100%;
	}

	&__input {
		flex: 0 1 100%;
		line-height: 36px;
		background-color: #F2F2F2;
		border: none;
		padding: 0 15px;

		input {
			display: none;
		}
	}

	&__input-inner {
		flex: 0 1 10%;

		input {
			width: 100%;
		}
	}

	&__button {
		padding: 0;
		width: 36px;
		height: 36px;
		border: none;
		background-color: #DFDFDF;
		flex: 0 0 36px;

		&_minus {
			border-radius: 4px 0 0 4px;
		}

		&_plus {
			border-radius: 0 4px 4px 0;
		}
	}
}
