.select {
	&_modal {
		.choices,
		.choices[data-type*="select-one"] {
			.choices {
				&__inner {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					pointer-events: none;
					//display: none;
					background-color: transparent;
					border: none;
					padding: 9px 28px;

					.choices__list {
						padding: 0;
					}

					.choices__item {
						padding: 0;
						border: none;
					}
				}

				&__input {
					border: none;
					border-bottom: 2px solid $color-crusta;
					color: #23b0f2;
					padding: 9px 28px;
				}

				&__list {
					position: static;
					display: block;
					border: none;
					max-height: 80vh;

					.choices__list {
						padding: 0 28px;
					}

					&--dropdown {
						.choices__placeholder {
							display: none;
						}
					}
				}

				&__heading {
					padding: 16px 0;
					border-bottom: 1px solid $color-1;
					font-size: 14px;
					font-weight: 400;
					line-height: 19px;
					color: $color-2;
				}

				&__item {
					padding: 16px 0;
					border-bottom: 1px solid $color-1;
					font-size: 14px;
					line-height: 19px;

					&.is-highlighted {
						background-color: transparent;
					}
				}
			}

			&.is-focused.is-open {
				.choices__list {
					&--single {
						.choices__item--selectable {
							display: none;
						}
					}
				}
			}

			&::after {
				display: none;
			}
		}
	}
}
