.date-input {
	height: 100%;
	display: flex;
	flex-flow: column nowrap;
	align-items: stretch;

	.flatpickr-input {
		display: none;
	}

	.flatpickr-wrapper {
		display: block;
		width: 100%;
	}

	.flatpickr-calendar {
		top: 0;
		width: 100% !important;
		max-width: none;
		border: none;
		border-radius: 0;
		box-shadow: none;
	}

	.flatpickr-calendar.inline,
	.flatpickr-calendar.open {
		max-height: none;
	}

	.flatpickr-weekdays {
		position: relative;
		z-index: 3;
		height: 38px;
	}

	.flatpickr-weekdaycontainer {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		display: none;
		padding: 10px 15px;
		background-color: #FF8336;
		height: 38px;

		&:first-child {
			display: flex;
		}
	}

	.flatpickr-weekday {
		color: $color-white;
		font-size: 12px;
		line-height: 16px;
	}

	.flatpickr-days {
		flex-flow: column nowrap;
		width: 100% !important;
		padding-top: 13px;
	}

	.flatpickr-calendar.multiMonth .flatpickr-days .dayContainer:nth-child(n+2) .flatpickr-day.inRange:nth-child(7n+1) {
		box-shadow: none;
	}

	.flatpickr-day {
		position: relative;
		z-index: 1;
		max-width: 14.2857143%;

		&.today {
			border: none;
		}

		&::before {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: -1;
			display: block;
			width: 32px;
			height: 32px;
			border-radius: 50%;
			background-color: #FF8336;
			opacity: 0;
			transform: translate(-50%, -50%);
		}

		&.selected,
		&.inRange {
			border: none;
			background-color: transparent;
			color: $color-black;
			box-shadow: none;
		}

		&.inRange {
			color: #ff8336;
		}

		&.startRange,
		&.endRange {
			&::before {
				opacity: 1;
			}
		}
	}

	.flatpickr-months {
		display: none;
	}

	.dayContainer {
		width: 100%;
		max-width: none;
		border-bottom: 1px solid #DFDFDF;
		margin-bottom: 13px;
		padding-left: 15px;
		padding-right: 15px;
		padding-bottom: 12px;
	}

	.flatpickr-innerContainer,
	.flatpickr-rContainer {
		width: 100%;
	}

	&__title {
		width: 100%;
		flex-basis: 100%;
		text-align: left;
		font-size: 14px;
		line-height: 19px;
		font-weight: 700;
		color: $color-black;
		margin-bottom: 14px;
		padding: 0 28px;
	}

	&__dates {
		z-index: 2;
		background-color: $color-white;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			bottom: 100%;
			display: block;
			background: linear-gradient(0deg, $color-white 0%, rgba(255, 255, 255, 0) 100%);
			height: 110px;
			pointer-events: none;
		}
	}

	&__input {
		flex: 0 1 100%;
		overflow: hidden;
		overflow-y: scroll;
	}

	&__dates {
		position: relative;
		flex: 0 0 auto;
		pointer-events: none;
		opacity: 0;
		transition: opacity 0.4s;

		&.visible {
			opacity: 1;
			pointer-events: auto;
		}
	}

	&__dates-name {
		font-size: 24px;
		line-height: 33px;
		font-weight: 700;
		margin-bottom: 19px;
	}

	&__separator {
		margin-top: 13px;
		width: 13px;
		height: 1px;
		background-color: $color-black;
	}

	&__date-big {
		font-size: 18px;
		line-height: 25px;
	}

	&__date-small {
		font-size: 12px;
		line-height: 16px;
	}
}
