.search-form-item {
	display: flex;
	margin-bottom: 20px;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
	border: 2px solid $color-concrete;
	border-radius: 4px;
	padding: 0 10px;
	font-family: $font-open-sans;
	font-size: 14px;
	line-height: 20px;
	background-color: $color-concrete;
	cursor: pointer;

	&__title {
		flex: 0 0 auto;
		margin-right: 7px;
		padding: 7px 0;
		color: $color-gray;
		pointer-events: none;
	}

	&__value {
		flex: 0 1 100%;
		border: none;
		color: $color-text;
		font-size: 14px;
		line-height: 20px;
		padding: 7px 0;
		min-height: 34px;
		background-color: transparent;
	}

	&:hover,
	&:focus,
	&:active,
	&.active {
		outline: none;
	}

	&:focus,
	&:active,
	&.active {
		border-color: $color-crusta;
	}
}
