.header-menu {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 99;
	overflow: hidden;
	overflow-y: scroll;
	background-color: $color-crusta;
	opacity: 0;
	pointer-events: none;
	transform: scale(0.9);
	transform-origin: center center;
	transition: transform 0.4s, opacity 0.6s;
	padding-top: 80px;

	a {
		color: $color-white;
		text-decoration: none;
	}

	&.active {
		opacity: 1;
		transform: scale(1);
		pointer-events: auto;
	}

	&__city {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		width: 100%;
		padding: 17px 30px;
		font-size: 16px;
		line-height: 22px;
		color: $color-curious-blue;
		background-color: $color-white;
		border: none;
		margin-bottom: 55px;
	}

	&__city-svg {
		margin-right: 8px;
		flex: 0 0 18px;
		width: 14px;
		height: 22px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__city-text {
		border-bottom: 1px dashed $color-curious-blue;
	}
}
