.form-input-modal {
	.modal-content {
		height: 100vh;
		overflow: hidden;
		overflow-y: scroll;
		padding-bottom: 90px;
	}

	&__title {
		padding: 0 28px;
		font-weight: 700;
		font-size: 14px;
		line-height: 19px;
		margin-bottom: 35px;
	}

	&__content {
		position: relative;
		height: 100%;
		overflow: hidden;
		overflow-y: scroll;
		z-index: 2;
	}

	&__title-icon {
		display: inline-block;
		vertical-align: middle;
		width: 15px;
		height: 20px;
		margin-right: 8px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__footer {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 3;
		height: 90px;
		padding: 27px 0;
		background-color: $color-white;

		.button {
			padding: 5px 20px;
			font-size: 14px;
			line-height: 26px;
		}
	}

	&_no-close {
		.modal-content {
			padding-top: 0;
		}
		.modal__close {
			display: none;
		}
	}
}
