.input-radio {
	&__content {
		position: relative;
		font-size: 14px;
		line-height: 19px;
		padding-left: 24px;
	}

	&__icon {
		position: absolute;
		left: 0;
		top: 50%;
		width: 14px;
		height: 14px;
		transform: translateY(-50%);
		border: 2px solid #B1B1B1;
		border-radius: 50%;

		&::before {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			opacity: 0;
			width: 8px;
			height: 8px;
			background-color: $color-crusta;
			border-radius: 50%;
			transform: translate(-50%, -50%);
			transition: opacity 0.2s;
		}
	}

	&__input {
		display: none;

		&:checked {
			~ .input-radio {
				&__content {
					.input-radio__icon {
						&::before {
							opacity: 1;
						}
					}
				}
			}
		}
	}
}
