// stylelint-disable scss/dollar-variable-empty-line-before

$grid-gutter-width: 8px;

// COLORS
$color-black: #000;
$color-white: #fff;
$color-curious-blue: #1391e1;
$color-brown-derby: #561f1b;
$color-vermilion: #ff4200;
$color-crusta: #ff8336;
$color-jungle-mist: #aeccd5;
$color-hoki: #5e8798;
$color-concrete: #f2f2f2;
$color-gray: #828282;

$primary: $color-curious-blue;
$secondary: $color-vermilion;
$color-text: #333;
$color-1: #ababab;
$color-2: #9A9A9A;

// FONTS
$font-open-sans: "Open Sans";

$spacer: 10px !default;
$spacers: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$spacers: map-merge(
		(
			0: 0,
			1: $spacer / 0.5,
			2: $spacer,
			3: ($spacer * 1.5),
			4: ($spacer * 2),
			5: ($spacer * 2.5),
			6: ($spacer * 3),
			7: ($spacer * 3.5),
			8: ($spacer * 4),
			9: ($spacer * 4.5),
			10: ($spacer * 5),
			11: ($spacer * 5.5),
			12: ($spacer * 6),
			13: ($spacer * 6.5),
			14: ($spacer * 7),
			15: ($spacer * 7.5),
			16: ($spacer * 8),
			17: ($spacer * 8.5),
			18: ($spacer * 9),
			19: ($spacer * 9.5),
			20: ($spacer * 10),
		),
		$spacers
);
