.modal {
	transform: scale(0.9);
	opacity: 0;
	transition: transform 0.2s, opacity 0.2s;
	transform-origin: center center;
	background-color: transparent !important;

	&.show {
		opacity: 1;
		transform: scale(1);
	}

	&-dialog {
		margin: 0;
		background: transparent;
	}

	&-content {
		border: none;
		border-radius: 0;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		min-height: 100vh;
		padding-top: 35px;
		background-color: $color-white;
		transform: none !important;
		margin: 0;
		z-index: 1;
	}

	&__close {
		position: absolute;
		right: 10px;
		top: 10px;
		z-index: 100;
		padding: 5px;
		font-size: 0;
		line-height: 0;

		svg {
			width: 13px;
			height: 13px;
		}
	}
}

.modal-backdrop {
	opacity: 0 !important;
}
