.slider {
	&__arrow {
		background: none;
		width: 20px;
		height: 20px;

		&_prev {
			left: 5px;
		}

		&_next {
			right: 5px;
		}

		svg {
			width: 100%;
			height: 100%;
		}
	}

	.swiper-pagination {
		bottom: 36px;
	}

	.swiper-pagination-bullet {
		width: 14px;
		height: 14px;
		margin: 0 8px !important;
		background-color: #c4c4c4;
		opacity: 1;

		&-active {
			background-color: #23B0F2;
		}
	}
}
