.button {
	display: inline-block;
	border: none;
	border-radius: 4px;
	padding: 10px 20px;
	font-size: 18px;
	line-height: 25px;
	text-align: center;
	box-shadow: none;

	&_block {
		display: block;
		width: 100%;
	}

	&_primary {
		background-color: $color-crusta;
		color: $color-white;
	}

	&_cancel {
		background-color: #DFDFDF;
		color: #565656;
	}

	&__sublink {
		display: inline-block;
		vertical-align: middle;
		text-decoration: underline;
	}

	&_secondary {
		background-color: #e4f6ff;
		color: #1391e1;
	}
}
